@mixin clearfix() {
  &:before,
  &:after {
    content: '';
    display: table;
    clear: both;
  }
}

@mixin min-max($min-width, $max-width, $padding) {
  min-width: $min-width;
  max-width: $max-width;
  padding: 0 $padding;
  box-sizing: border-box;
  margin: 0 auto;
}

@mixin adaptive-video() {
  position: relative;
  padding-bottom: 56.25%; /* задаёт высоту контейнера для 16:9 (если 4:3 — поставьте 75%) */
  padding-top: 30px;
  height: 0;
  overflow: hidden;

  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

}

@mixin visually-hidden() {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0,0,0,0);
  overflow: hidden;
}

@mixin logo() {
  transition: all 0.1s linear;

  &:hover {
    transform: scale(1.03);
  }
}

@mixin button() {
  border: 0;
  appearance: none;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.05);
  transition: all 0.1s ease;

  &--red {
    background: $color-red;
    color: $color-white;
    padding: 13px 54px 15px;
    border-radius: 5px;

    &:hover {
      background: darken($color-red, 10%);
    }
  }
}

@mixin section-title() {
  margin: 0 0 60px;
  font-size: 1.25rem;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}